body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-style ul {
  display: flex;
  list-style: none;
  justify-content: center;
}

.nav-style li {
  margin-right: 20px;
  color: white;
}

.nav-style a {
  color: white;
  text-decoration: underline !important;
}

.App {
  text-align: center; }

.App-header {
  background-color: #9fb4df;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: black; }

a {
  text-decoration: none;
  color: yellow; }

.navbar {
  margin-bottom: 120px; }

.task-form {
  margin-bottom: 40px;
  margin-top: 40px; }

.task-box {
  margin-bottom: 20px;
  width: 340px; }

.task-box h3, .task-box p {
  margin: 10px 0; }

.nav-style .header {
	display: flex;
	justify-content: center;
	align-items: center;
}

.nav-style .header img {
	margin-right: 20px;
}
